@import '../../../assets/css/base.scss';

.sort-sec {
    margin: 0 5px 0 0;
}


.toggle-button {
    display: flex;
    align-items: center;
    color: var(--white-color);
    input[type=checkbox]{
      height: 0;
      width: 0;
      visibility: hidden;
    }
    label {
      cursor: pointer;
      text-indent: -9999px;
      width: 70px;
      height: 30px;
      background: grey;
      display: block;
      border-radius: 70px;
      position: relative;
    }
    label:after {
      content: '';
      position: absolute;
      top: 5px;
      left: 5px;
      width: 20px;
      height: 20px;
      border-radius: 50px;
      transition: 0.3s;
      background: var(--white-color);
    }
    span {
      color: rgba(255,255,255,.5);
      margin-left: 10px;
    }
    input:checked + label {
      background: var(--purple-color);   
    }
    input:checked + label + span {     
      color: var(--white-color);
    }  
    input:checked + label:after {
      left: calc(100% - 5px);
      transform: translateX(-100%);
    }  
  }