@import "../../../assets/css/base.scss";

.front_main {
  height: 100%;
}

.sidebar_pic {
  height: 100vh;
  overflow: $hidden;
  @media (max-width: 767px) {
    display: $none;
  }
  figure {
    position: $fixed;
    width: 50%;
    top: 0;
    left: 0;
    height: 100%;
    img {
      position: $absolute;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

.login_title_group {
  color: $purple-color;
  margin: 40px 0 30px;
  h1 {
    font-weight: $font-regular;
    color: $inherit;
  }
  h4 {
    color: $inherit;
  }
}

.ls_form {
  padding: 40px;
  margin: 0 $auto;
  max-width: 500px;
  @media (max-width: 575px) {
    padding: 20px;
  }
  label {
    color: $primary-color;
  }
  .form-control {
    border-style: solid;
    border-width: 0 0 1px 0;
    border-color: $grey-color;
    padding: 10px 0;
    @include border-radius(0);
    &:focus {
      border-width: 0 0 1px 0;
    }
  }
}

.visibility-Eye {
  position: absolute;
  margin-top: -35px;
  margin-left: 360px;
}
